import React from 'react';
import { useIntl } from 'react-intl';
import { Link, LinkProps } from '@reach/router';
import useMobileView from '../../hooks/useMobileView';
import useWidthViewPort from '../../hooks/useWidthViewPort';

import './styles.css';

import {
  ROUTE_ABOUT,
  ROUTE_SOFT_SKILLS,
  ROUTE_CURRICULUM,
  ROUTE_TECH_SKILLS,
} from '../../util/constants';

const TEXT_BREAkPOINT = 397;

function Menu() {
  const intl = useIntl();
  const isMobileView = useMobileView();
  const currentVW = useWidthViewPort();

  function buildClassName(isCurrent: boolean) {
    let className = isMobileView ? 'menu-item menu-item-mobile' : 'menu-item';
    return isCurrent ? className + ' menu-item-active' : className;
  }

  const NavLink = (
    props: React.PropsWithoutRef<LinkProps<any>> &
      React.RefAttributes<HTMLAnchorElement>
  ) => (
    <Link
      {...props}
      getProps={({ isCurrent }) => {
        return {
          className: buildClassName(isCurrent),
        };
      }}
    />
  );

  return (
    <nav id='menu'>
      <NavLink
        to={ROUTE_ABOUT}
        className='menu-item'
        title={intl.formatMessage({ id: 'about' })}
      >
        {intl.formatMessage({ id: 'about' })}
      </NavLink>
      <NavLink
        to={ROUTE_SOFT_SKILLS}
        className='menu-item'
        title={
          currentVW <= TEXT_BREAkPOINT
            ? intl.formatMessage({ id: 'softSkillsSort' })
            : intl.formatMessage({ id: 'softSkills' })
        }
      >
        {currentVW <= TEXT_BREAkPOINT
          ? intl.formatMessage({ id: 'softSkillsSort' })
          : intl.formatMessage({ id: 'softSkills' })}
      </NavLink>
      <NavLink
        to={ROUTE_CURRICULUM}
        className='menu-item'
        title={intl.formatMessage({ id: 'curriculum' })}
      >
        {intl.formatMessage({ id: 'curriculum' })}
      </NavLink>
      <NavLink
        to={ROUTE_TECH_SKILLS}
        className='menu-item'
        title={
          currentVW <= TEXT_BREAkPOINT
            ? intl.formatMessage({ id: 'TechSkillsSort' })
            : intl.formatMessage({ id: 'techSkills' })
        }
      >
        {currentVW <= TEXT_BREAkPOINT
          ? intl.formatMessage({ id: 'techSkillsSort' })
          : intl.formatMessage({ id: 'techSkills' })}
      </NavLink>
    </nav>
  );
}

export default Menu;
