// #TODO: Pesquisar sobre retirar o import 'React'.
import React, { useState } from 'react';
import { useIntl } from 'react-intl';
import { RouteComponentProps } from '@reach/router';
import useMobileView from '../../hooks/useMobileView';

import SectionHeader from '../SectionHeader';

import { LINKEDIN_PROFILE } from '../../util/constants';

//@ts-ignore
import CV from '../../assets/curriculum/rv.pdf';
import DownlodIcon from '../../assets/images/download-icon.svg';
import ResumeTimeLineOpen from '../../assets/images/resume-time-line-open.svg';
import ResumeTimeLineClose from '../../assets/images/resume-time-line-close.svg';
import './styles.css';

interface CurriculumProps extends RouteComponentProps {}

function Curriculum(props: CurriculumProps) {
  const intl = useIntl();
  const isMobileView = useMobileView();
  const [exp1Expanded, setExp1Expanded] = useState(false);
  const [exp2Expanded, setExp2Expanded] = useState(false);
  const [exp3Expanded, setExp3Expanded] = useState(false);
  const [exp4Expanded, setExp4Expanded] = useState(false);
  const [exp5Expanded, setExp5Expanded] = useState(false);
  const [exp6Expanded, setExp6Expanded] = useState(false);

  const titleContent = (
    title: string,
    subTitle: string,
    expanded: boolean,
    setExpanded: Function
  ) => (
    <div className='curriculum-title-content'>
      <h1 className='title' onClick={() => setExpanded(!expanded)}>
        {title}
      </h1>
      <span className='small-text' onClick={() => setExpanded(!expanded)}>
        {subTitle}
      </span>
    </div>
  );

  return (
    <article id='resume' className='content'>
      <header className='custom-header'>
        <h1 className='page-title'>
          {intl.formatMessage({ id: 'resumeTitle' })}
        </h1>
      </header>
      {isMobileView ? (
        <>
          <section>
            <SectionHeader
              titleContent={titleContent(
                intl.formatMessage({ id: 'experience1Title' }),
                intl.formatMessage({ id: 'experience1SubTitle' }),
                exp1Expanded,
                setExp1Expanded
              )}
              expanded={exp1Expanded}
              setExpanded={setExp1Expanded}
            />
            {exp1Expanded ? (
              <p>{intl.formatMessage({ id: 'experience1Text' })}</p>
            ) : (
              ''
            )}
          </section>
          <section>
            <SectionHeader
              titleContent={titleContent(
                intl.formatMessage({ id: 'experience2Title' }),
                intl.formatMessage({ id: 'experience2SubTitle' }),
                exp2Expanded,
                setExp2Expanded
              )}
              expanded={exp2Expanded}
              setExpanded={setExp2Expanded}
            />
            {exp2Expanded ? (
              <p>{intl.formatMessage({ id: 'experience2Text' })}</p>
            ) : (
              ''
            )}
          </section>
          <section>
            <SectionHeader
              titleContent={titleContent(
                intl.formatMessage({ id: 'experience3Title' }),
                intl.formatMessage({ id: 'experience3SubTitle' }),
                exp3Expanded,
                setExp3Expanded
              )}
              expanded={exp3Expanded}
              setExpanded={setExp3Expanded}
            />
            {exp3Expanded ? (
              <p>{intl.formatMessage({ id: 'experience3Text' })}</p>
            ) : (
              ''
            )}
          </section>
          <section>
            <SectionHeader
              titleContent={titleContent(
                intl.formatMessage({ id: 'experience4Title' }),
                intl.formatMessage({ id: 'experience4SubTitle' }),
                exp4Expanded,
                setExp4Expanded
              )}
              expanded={exp4Expanded}
              setExpanded={setExp4Expanded}
            />
            {exp4Expanded ? (
              <p>{intl.formatMessage({ id: 'experience4Text' })}</p>
            ) : (
              ''
            )}
          </section>
          <section>
            <SectionHeader
              titleContent={titleContent(
                intl.formatMessage({ id: 'experience5Title' }),
                intl.formatMessage({ id: 'experience5SubTitle' }),
                exp5Expanded,
                setExp5Expanded
              )}
              expanded={exp5Expanded}
              setExpanded={setExp5Expanded}
            />
            {exp5Expanded ? (
              <p>{intl.formatMessage({ id: 'experience5Text' })}</p>
            ) : (
              ''
            )}
          </section>
          <section>
            <SectionHeader
              titleContent={titleContent(
                intl.formatMessage({ id: 'experience6Title' }),
                intl.formatMessage({ id: 'experience6SubTitle' }),
                exp6Expanded,
                setExp6Expanded
              )}
              expanded={exp6Expanded}
              setExpanded={setExp6Expanded}
            />
            {exp6Expanded ? (
              <p>{intl.formatMessage({ id: 'experience6Text' })}</p>
            ) : (
              ''
            )}
          </section>
        </>
      ) : (
        <>
          <section className='experience-section'>
            <img src={ResumeTimeLineClose} alt='|' />
            <div>
              <header>
                <h1 className='title'>
                  {intl.formatMessage({ id: 'experience1Title' })}
                </h1>
                <h2>{intl.formatMessage({ id: 'experience1SubTitle' })}</h2>
              </header>
              <p>{intl.formatMessage({ id: 'experience1Text' })}</p>
            </div>
          </section>
          <section className='experience-section'>
            <img src={ResumeTimeLineOpen} alt='|' />
            <div>
              <header>
                <h1 className='title'>
                  {intl.formatMessage({ id: 'experience2Title' })}
                </h1>
                <h2>{intl.formatMessage({ id: 'experience2SubTitle' })}</h2>
              </header>
              <p>{intl.formatMessage({ id: 'experience2Text' })}</p>
            </div>
          </section>
          <section className='experience-section'>
            <img src={ResumeTimeLineOpen} alt='|' />
            <div>
              <header>
                <h1 className='title'>
                  {intl.formatMessage({ id: 'experience3Title' })}
                </h1>
                <h2>{intl.formatMessage({ id: 'experience3SubTitle' })}</h2>
              </header>
              <p>{intl.formatMessage({ id: 'experience3Text' })}</p>
            </div>
          </section>
          <section className='experience-section'>
            <img src={ResumeTimeLineOpen} alt='|' />
            <div>
              <header>
                <h1 className='title'>
                  {intl.formatMessage({ id: 'experience4Title' })}
                </h1>
                <h2>{intl.formatMessage({ id: 'experience4SubTitle' })}</h2>
              </header>
              <p>{intl.formatMessage({ id: 'experience4Text' })}</p>
            </div>
          </section>
          <section className='experience-section'>
            <img src={ResumeTimeLineOpen} alt='|' />
            <div>
              <header>
                <h1 className='title'>
                  {intl.formatMessage({ id: 'experience5Title' })}
                </h1>
                <h2>{intl.formatMessage({ id: 'experience5SubTitle' })}</h2>
              </header>
              <p>{intl.formatMessage({ id: 'experience5Text' })}</p>
            </div>
          </section>
          <section className='experience-section'>
            <img src={ResumeTimeLineOpen} alt='|' />
            <div>
              <header>
                <h1 className='title'>
                  {intl.formatMessage({ id: 'experience6Title' })}
                </h1>
                <h2>{intl.formatMessage({ id: 'experience6SubTitle' })}</h2>
              </header>
              <p>{intl.formatMessage({ id: 'experience6Text' })}</p>
            </div>
          </section>
        </>
      )}
      <footer>
        <a href={CV} className='cv-btn' download='ricardo-vargas-cv.pdf'>
          <span className='bg-degrade'>
            <span>{intl.formatMessage({ id: 'downloadBtn' })}</span>
            <img src={DownlodIcon} alt={'download'} />
          </span>
        </a>
        &nbsp;
        <span className='cv-more-detail'>
          {intl.formatMessage({ id: 'otherDetails1' })}&nbsp;
          <a
            rel='noreferrer'
            href={LINKEDIN_PROFILE}
            target='_blank'
            title={intl.formatMessage({ id: 'linkedin' })}
          >
            {intl.formatMessage({ id: 'otherDetails2' })}
          </a>
        </span>
      </footer>
    </article>
  );
}

export default Curriculum;
