import React from 'react';
import { useIntl } from 'react-intl';
import useMobileView from '../../hooks/useMobileView';

import SocialIcons from '../SocialIcons/';
import Menu from '../Menu';

import { RICARDO_EMAIL } from '../../util/constants';
import './styles.css';

function Footer() {
  const intl = useIntl();
  const isMobileView = useMobileView();

  return (
    <footer id='footer'>
      <section className='content content-no-padding'>
        {isMobileView ? (
          <Menu />
        ) : (
          <>
            <div>
              <span>{intl.formatMessage({ id: 'developedBy' })}</span>
              <span>
                {intl.formatMessage({ id: 'contact' })}&nbsp;
                <a
                  className='default-link'
                  href={`mailto:${RICARDO_EMAIL}`}
                  title={intl.formatMessage({ id: 'emailTitle' })}
                >
                  {RICARDO_EMAIL}
                </a>
              </span>
            </div>
            <SocialIcons />
          </>
        )}
      </section>
    </footer>
  );
}

export default Footer;
