import React from 'react';
import { useIntl } from 'react-intl';

import './styles.css';
import LinkedinIcon from '../../assets/images/linkedin-mobile.svg';
import GithubIcon from '../../assets/images/github-mobile.svg';
import TwitterIcon from '../../assets/images/twitter-mobile.svg';

import {
  LINKEDIN_PROFILE,
  GITHUB_PROFILE,
  TWITTER_PROFILE,
} from '../../util/constants';

function SocialIcons() {
  const intl = useIntl();

  return (
    <div className='social-icons'>
      <a
        href={LINKEDIN_PROFILE}
        rel='noreferrer'
        target='_blank'
        title={intl.formatMessage({ id: 'linkedin' })}
      >
        <img
          src={LinkedinIcon}
          alt={intl.formatMessage({ id: 'linkedinAlt' })}
        />
      </a>
      <a
        href={GITHUB_PROFILE}
        rel='noreferrer'
        target='_blank'
        title={intl.formatMessage({ id: 'github' })}
      >
        <img src={GithubIcon} alt={intl.formatMessage({ id: 'githubAlt' })} />
      </a>
      <a
        href={TWITTER_PROFILE}
        rel='noreferrer'
        target='_blank'
        title={intl.formatMessage({ id: 'twitter' })}
      >
        <img src={TwitterIcon} alt={intl.formatMessage({ id: 'twitterAlt' })} />
      </a>
    </div>
  );
}

export default SocialIcons;
