// #TODO: Pesquisar sobre retirar o import 'React'.
import React from 'react';
import { useIntl } from 'react-intl';
import { RouteComponentProps } from '@reach/router';
import useMobileView from '../../hooks/useMobileView';
import useWidthViewPort from '../../hooks/useWidthViewPort';

import ProfilePicSquare from '../../assets/images/profile-pic-square.jpg';
import './styles.css';

interface HomeProps extends RouteComponentProps {}

function Home(props: HomeProps) {
  const intl = useIntl();
  const isMobileView = useMobileView();
  const currentWVP = useWidthViewPort();

  return (
    <article id='introduction' className='content'>
      {isMobileView ? (
        <>
          <header className='mobile-header'>
            <img
              src={ProfilePicSquare}
              alt={intl.formatMessage({ id: 'profilePic' })}
            />
            <div>
              <h1 className='page-title'>
                {intl.formatMessage({ id: 'homeTitle' })}
              </h1>
              <h2 className='page-sub-title'>
                {currentWVP <= 400
                  ? intl.formatMessage({ id: 'homeSubTitleReduced' })
                  : intl.formatMessage({ id: 'homeSubTitle' })}
              </h2>
            </div>
          </header>
          <p>{intl.formatMessage({ id: 'homeTextPart1' })}</p>
          <p>{intl.formatMessage({ id: 'homeTextPart2' })}</p>
        </>
      ) : (
        <>
          <div className='introduction-content'>
            <header className='custom-header'>
              <h1 className='page-title page-title-left'>
                {intl.formatMessage({ id: 'homeTitle' })}
              </h1>
              <h2 className='page-sub-title'>
                {intl.formatMessage({ id: 'homeSubTitle' })}
              </h2>
            </header>
            <p>{intl.formatMessage({ id: 'homeTextPart1' })}</p>
            <p>{intl.formatMessage({ id: 'homeTextPart2' })}</p>
          </div>
          <footer>
            <img
              src={ProfilePicSquare}
              alt={intl.formatMessage({ id: 'profilePic' })}
            />
          </footer>
          <div className='clear-float'></div>
        </>
      )}
    </article>
  );
}

export default Home;
