import React from 'react';
import { RouteComponentProps, Link } from '@reach/router';
import { useIntl } from 'react-intl';
import useMobileView from '../../hooks/useMobileView';

import Menu from '../Menu';
import SocialIcons from '../SocialIcons';

import logo from '../../assets/images/logo-mobile.svg';
import './styles.css';

import { ROUTE_DEFAULT } from '../../util/constants';

interface HeaderProps extends RouteComponentProps {
  LangPickerComponent: React.ReactNode;
}

function Header({ LangPickerComponent }: HeaderProps) {
  const intl = useIntl();

  return (
    <header id='header'>
      <section className='content'>
        <Link to={ROUTE_DEFAULT} className='logo-link'>
          <img
            src={logo}
            title={intl.formatMessage({ id: 'ricardoVargasHome' })}
            alt={intl.formatMessage({ id: 'ricardoVargasHomeAlt' })}
          />
        </Link>
        {useMobileView() ? <SocialIcons /> : <Menu />}
        {LangPickerComponent}
      </section>
    </header>
  );
}

export default Header;
