import React from 'react';
import { useIntl } from 'react-intl';
import { RouteComponentProps } from '@reach/router';

import arrowExpandClose from '../../assets/images/arrow-expand-close.svg';
import arrowExpandOpen from '../../assets/images/arrow-expand-open.svg';

import './styles.css';

interface SectionHeadertProps extends RouteComponentProps {
  expanded: boolean;
  setExpanded: Function;
  titleContent?: React.ReactNode;
  title?: string;
}

function SectionHeader({
  expanded,
  setExpanded,
  titleContent,
  title,
}: SectionHeadertProps) {
  const intl = useIntl();

  return (
    <>
      <header className='section-header'>
        <img
          src={expanded ? arrowExpandOpen : arrowExpandClose}
          alt={
            expanded
              ? intl.formatMessage({ id: 'expandSection' })
              : intl.formatMessage({ id: 'hideSection' })
          }
          title={
            expanded
              ? intl.formatMessage({ id: 'expandSection' })
              : intl.formatMessage({ id: 'hideSection' })
          }
          onClick={() => setExpanded(!expanded)}
        />
        {titleContent ? (
          <>{titleContent}</>
        ) : (
          <h1 className='page-title' onClick={() => setExpanded(!expanded)}>
            {title}
          </h1>
        )}
        <hr />
      </header>
    </>
  );
}

export default SectionHeader;
