// #TODO: Pesquisar sobre retirar o import 'React'.
import React from 'react';
import { useIntl } from 'react-intl';
import { RouteComponentProps } from '@reach/router';
import useWidthViewPort from '../../hooks/useWidthViewPort';

import './styles.css';

interface TechSkillsProps extends RouteComponentProps {}

/*#TODO: Corrigir o layout que está quebrando em resoluções abaixo de 350px.*/

function TechSkills(props: TechSkillsProps) {
  const intl = useIntl();
  const isMobileView = useWidthViewPort() <= 801;

  const buildSkillBlock = (title: string, percentDesc: string) => (
    <div className='skill-description'>
      <span className='skill-title-bg' style={{ width: percentDesc }}></span>
      <span className='skill-title'>{title}</span>
      <span className='skill-percent'>{percentDesc}</span>
    </div>
  );

  return (
    <article id='tech-skills' className='content'>
      <header className='custom-header'>
        <h1 className='page-title'>
          {intl.formatMessage({ id: 'techSkillsTitle' })}
        </h1>
      </header>
      <ul className='left-block'>
        <li>
          {buildSkillBlock(
            intl.formatMessage({ id: 'html' }),
            intl.formatMessage({ id: 'htmlPercent' })
          )}
        </li>
        <li>
          {buildSkillBlock(
            intl.formatMessage({ id: 'css' }),
            intl.formatMessage({ id: 'cssPercent' })
          )}
        </li>
        <li>
          {buildSkillBlock(
            intl.formatMessage({ id: 'javascript' }),
            intl.formatMessage({ id: 'javascriptPercent' })
          )}
        </li>
        <li>
          {buildSkillBlock(
            intl.formatMessage({ id: 'sass' }),
            intl.formatMessage({ id: 'sassPercent' })
          )}
        </li>
        <li>
          {buildSkillBlock(
            intl.formatMessage({ id: 'typescript' }),
            intl.formatMessage({ id: 'typescriptPercent' })
          )}
        </li>
        <li>
          {buildSkillBlock(
            intl.formatMessage({ id: 'react' }),
            intl.formatMessage({ id: 'reactPercent' })
          )}
        </li>
        <li>
          {buildSkillBlock(
            intl.formatMessage({ id: 'reactRouter' }),
            intl.formatMessage({ id: 'reactRouterPercent' })
          )}
        </li>
        <li>
          {buildSkillBlock(
            intl.formatMessage({ id: 'reactIntl' }),
            intl.formatMessage({ id: 'reactIntlPercent' })
          )}
        </li>
        <li>
          {buildSkillBlock(
            intl.formatMessage({ id: 'redux' }),
            intl.formatMessage({ id: 'reduxPercent' })
          )}
        </li>
        <li>
          {buildSkillBlock(
            intl.formatMessage({ id: 'reduxForm' }),
            intl.formatMessage({ id: 'reduxFormPercent' })
          )}
        </li>
        <li>
          {buildSkillBlock(
            intl.formatMessage({ id: 'reactHookForm' }),
            intl.formatMessage({ id: 'reactHookFormPercent' })
          )}
        </li>
      </ul>
      <ul className='right-block'>
        <li>
          {buildSkillBlock(
            intl.formatMessage({ id: 'materialUi' }),
            intl.formatMessage({ id: 'materialUiPercent' })
          )}
        </li>
        <li>
          {buildSkillBlock(
            intl.formatMessage({ id: 'bootstrap' }),
            intl.formatMessage({ id: 'bootstrapPercent' })
          )}
        </li>
        <li>
          {buildSkillBlock(
            intl.formatMessage({ id: 'styledComponents' }),
            intl.formatMessage({ id: 'styledComponentsPercent' })
          )}
        </li>
        <li>
          {buildSkillBlock(
            intl.formatMessage({ id: 'axios' }),
            intl.formatMessage({ id: 'axiosPercent' })
          )}
        </li>
        <li>
          {buildSkillBlock(
            intl.formatMessage({ id: 'npm' }),
            intl.formatMessage({ id: 'npmPercent' })
          )}
        </li>
        <li>
          {buildSkillBlock(
            intl.formatMessage({ id: 'yarn' }),
            intl.formatMessage({ id: 'yarnPercent' })
          )}
        </li>
        <li>
          {buildSkillBlock(
            intl.formatMessage({ id: 'php' }),
            intl.formatMessage({ id: 'phpPercent' })
          )}
        </li>
        <li>
          {buildSkillBlock(
            intl.formatMessage({ id: 'nodeJs' }),
            intl.formatMessage({ id: 'nodeJsPercent' })
          )}
        </li>
        <li>
          {buildSkillBlock(
            intl.formatMessage({ id: 'git' }),
            intl.formatMessage({ id: 'gitPercent' })
          )}
        </li>
        <li>
          {buildSkillBlock(
            intl.formatMessage({ id: 'illustrator' }),
            intl.formatMessage({ id: 'illustratorPercent' })
          )}
        </li>
      </ul>
      {isMobileView ? '' : <div className='div-clear-float'></div>}
    </article>
  );
}

export default TechSkills;
