import React from 'react';
import { useIntl } from 'react-intl';
import { RouteComponentProps } from '@reach/router';

import CommunicationIcon from '../../assets/images/soft-skills-communication.svg';
import TeamWorkIcon from '../../assets/images/soft-skills-teamwork.svg';
import CreativeMindIcon from '../../assets/images/soft-skills-creative-mind.svg';
import BilingualIcon from '../../assets/images/soft-skills-bilingual.svg';
import SolvingProblemsIcon from '../../assets/images/soft-skills-solving-problems.svg';
import FlexibleIcon from '../../assets/images/soft-skills-flexible.svg';
import EmphatyIcon from '../../assets/images/soft-skills-emphaty.svg';
import AccountabilityIcon from '../../assets/images/soft-skills-accountability.svg';
import './styles.css';

interface SoftSkillProps extends RouteComponentProps {}

function SoftSkills(props: SoftSkillProps) {
  const intl = useIntl();

  return (
    <article id='softSkills' className='content'>
      <header className='custom-header'>
        <h1 className='page-title'>
          {intl.formatMessage({ id: 'softSkillsTitle' })}
        </h1>
      </header>
      <ul>
        <li>
          <div className='div-img'>
            <img
              src={CommunicationIcon}
              alt={intl.formatMessage({ id: 'comunicationImgAlt' })}
              title={intl.formatMessage({ id: 'comunicationTitle' })}
            />
          </div>
          <div>
            <h2 className='title'>
              {intl.formatMessage({ id: 'comunicationTitle' })}
            </h2>
            <p>{intl.formatMessage({ id: 'comunicationText' })}</p>
          </div>
        </li>
        <li>
          <div className='div-img'>
            <img
              src={TeamWorkIcon}
              alt={intl.formatMessage({ id: 'teamWorkImgAlt' })}
              title={intl.formatMessage({ id: 'teamWorkTitle' })}
            />
          </div>
          <div>
            <h2 className='title'>
              {intl.formatMessage({ id: 'teamWorkTitle' })}
            </h2>
            <p>{intl.formatMessage({ id: 'teamWorkText' })}</p>
          </div>
        </li>
        <li>
          <div className='div-img'>
            <img
              src={CreativeMindIcon}
              alt={intl.formatMessage({ id: 'creativityImgAlt' })}
              title={intl.formatMessage({ id: 'creativityTitle' })}
            />
          </div>
          <div>
            <h2 className='title'>
              {intl.formatMessage({ id: 'creativityTitle' })}
            </h2>
            <p>{intl.formatMessage({ id: 'creativityText' })}</p>
          </div>
        </li>
        <li>
          <div className='div-img'>
            <img
              src={BilingualIcon}
              alt={intl.formatMessage({ id: 'bilingualImgAlt' })}
              title={intl.formatMessage({ id: 'bilingualTitle' })}
            />
          </div>
          <div>
            <h2 className='title'>
              {intl.formatMessage({ id: 'bilingualTitle' })}
            </h2>
            <p>{intl.formatMessage({ id: 'bilingualText' })}</p>
          </div>
        </li>
        <li>
          <div className='div-img'>
            <img
              src={SolvingProblemsIcon}
              alt={intl.formatMessage({ id: 'solvingProblemsImgAlt' })}
              title={intl.formatMessage({ id: 'solvingProblemsTitle' })}
            />
          </div>
          <div>
            <h2 className='title'>
              {intl.formatMessage({ id: 'solvingProblemsTitle' })}
            </h2>
            <p>{intl.formatMessage({ id: 'solvingProblemsText' })}</p>
          </div>
        </li>
        <li>
          <div className='div-img'>
            <img
              src={FlexibleIcon}
              alt={intl.formatMessage({ id: 'flexibleImgAlt' })}
              title={intl.formatMessage({ id: 'flexibleTitle' })}
            />
          </div>
          <div>
            <h2 className='title'>
              {intl.formatMessage({ id: 'flexibleTitle' })}
            </h2>
            <p>{intl.formatMessage({ id: 'flexibleText' })}</p>
          </div>
        </li>
        <li>
          <div className='div-img'>
            <img
              src={EmphatyIcon}
              alt={intl.formatMessage({ id: 'emphatyImgAlt' })}
              title={intl.formatMessage({ id: 'empathyTitle' })}
            />
          </div>
          <div>
            <h2 className='title'>
              {intl.formatMessage({ id: 'empathyTitle' })}
            </h2>
            <p>{intl.formatMessage({ id: 'empathyText' })}</p>
          </div>
        </li>
        <li>
          <div className='div-img'>
            <img
              src={AccountabilityIcon}
              alt={intl.formatMessage({ id: 'accountabilityImgAlt' })}
              title={intl.formatMessage({ id: 'accountabilityTitle' })}
            />
          </div>
          <div>
            <h2 className='title'>
              {intl.formatMessage({ id: 'accountabilityTitle' })}
            </h2>
            <p>{intl.formatMessage({ id: 'accountabilityText' })}</p>
          </div>
        </li>
      </ul>
    </article>
  );
}

export default SoftSkills;
