// #TODO: Pesquisar sobre retirar o import 'React'.
import React, { useState } from 'react';
import { useIntl } from 'react-intl';
import { RouteComponentProps } from '@reach/router';
import useMobileView from '../../hooks/useMobileView';
import useWidthViewPort from '../../hooks/useWidthViewPort';

import { Image } from '../ImageGallery';

import SectionHeader from '../SectionHeader';
import ImageGallery from '../../components/ImageGallery';

import './styles.css';

interface AboutProps extends RouteComponentProps {}

function About(props: AboutProps) {
  const intl = useIntl();
  const isMobileView = useMobileView();
  const currentVW = useWidthViewPort();
  const [aboutExpanded, setAboutExpanded] = useState(false);
  const [momentsExpanded, setMomentsExpanded] = useState(false);
  const imageList: Image[] = [
    {
      imageType: 'firstGuinness',
      thumbTitle: intl.formatMessage({ id: 'dublinCardTitle' }),
      title: intl.formatMessage({ id: 'dublinCardTitle' }),
      alt: intl.formatMessage({ id: 'dublinCardTitle' }),
      description:
        currentVW <= 320
          ? intl.formatMessage({ id: 'dublinCardTextReduced' })
          : intl.formatMessage({ id: 'dublinCardText' }),
    },
    {
      imageType: 'iceland',
      thumbTitle: intl.formatMessage({ id: 'icelandCardTitle' }),
      title: intl.formatMessage({ id: 'icelandCardTitle' }),
      alt: intl.formatMessage({ id: 'icelandCardTitle' }),
      description:
        currentVW <= 320
          ? intl.formatMessage({ id: 'icelandCardTextReduced' })
          : intl.formatMessage({ id: 'icelandCardText' }),
    },
    {
      imageType: 'theWicklowWay',
      thumbTitle: intl.formatMessage({ id: 'theWicklowWayCardTitle' }),
      title: intl.formatMessage({ id: 'theWicklowWayCardTitle' }),
      alt: intl.formatMessage({ id: 'theWicklowWayCardTitle' }),
      description: (
        <>
          <a
            href='https://www.wicklowway.com/'
            target='_blank'
            rel='noreferrer'
            title={intl.formatMessage({ id: 'theWicklowWayCardText' })}
          >
            {intl.formatMessage({ id: 'theWicklowWayCardTitle' })}
          </a>
          &nbsp;
          {currentVW <= 320
            ? intl.formatMessage({ id: 'theWicklowWayCardTextReduced' })
            : intl.formatMessage({ id: 'theWicklowWayCardText' })}
        </>
      ),
    },
    {
      imageType: 'barbara',
      thumbTitle: intl.formatMessage({ id: 'barbaraBornCardTitle' }),
      title: intl.formatMessage({ id: 'barbaraBornCardTitle' }),
      alt: intl.formatMessage({ id: 'barbaraBornCardTitle' }),
      description: intl.formatMessage({ id: 'barbaraBornCardText' }),
    },
    {
      imageType: 'family',
      thumbTitle: intl.formatMessage({ id: 'goingOutCardTitle' }),
      title: intl.formatMessage({ id: 'goingOutCardTitle' }),
      alt: intl.formatMessage({ id: 'goingOutCardTitle' }),
      description: intl.formatMessage({ id: 'goingOutCardText' }),
    },
  ];

  const aboutContent = () => (
    <>
      <p>{intl.formatMessage({ id: 'contentFirstParagraph' })}</p>
      <p>{intl.formatMessage({ id: 'contentSecondParagraph' })}</p>
      <p>{intl.formatMessage({ id: 'contentThirdParagraph' })}</p>
      <p>{intl.formatMessage({ id: 'contentForthParagraph' })}</p>
    </>
  );

  return (
    <article id='about' className='content'>
      {isMobileView ? (
        <>
          <section>
            <SectionHeader
              title={intl.formatMessage({ id: 'aboutTitle' })}
              expanded={aboutExpanded}
              setExpanded={setAboutExpanded}
            />
            {aboutExpanded ? aboutContent() : ''}
          </section>
          <section>
            <SectionHeader
              title={intl.formatMessage({ id: 'memorableMoments' })}
              expanded={momentsExpanded}
              setExpanded={setMomentsExpanded}
            />
            {momentsExpanded ? <ImageGallery imageList={imageList} /> : ''}
          </section>
        </>
      ) : (
        <>
          <header className='custom-header'>
            <h1 className='page-title'>
              {intl.formatMessage({ id: 'aboutTitle' })}
            </h1>
          </header>
          <section>{aboutContent()}</section>
          <section>
            <h2 className='title'>
              {intl.formatMessage({ id: 'memorableMoments' })}
            </h2>
            <ImageGallery imageList={imageList} />
          </section>
        </>
      )}
    </article>
  );
}

export default About;
