import React, { useState, useEffect, ReactElement } from 'react';
import { RouteComponentProps } from '@reach/router';
import useWidthViewPort from '../../hooks/useWidthViewPort';

import './styles.css';

import firstGuinnessPic from '../../assets/images/memorable-moments/first-guinness-min.png';
import icelandPic from '../../assets/images/memorable-moments/iceland-min.png';
import theWicklowWayPic from '../../assets/images/memorable-moments/the-wicklow-way-min.png';
import barbaraPic from '../../assets/images/memorable-moments/barbara-min.png';
import familyPic from '../../assets/images/memorable-moments/family-min.png';
import iconClose from '../../assets/images/close.svg';
import iconNext from '../../assets/images/icon-next.svg';
import iconPrevious from '../../assets/images/icon-previous.svg';

export type ImageType =
  | 'firstGuinness'
  | 'iceland'
  | 'theWicklowWay'
  | 'barbara'
  | 'family';

export type Image = {
  imageType: ImageType;
  thumbTitle: string;
  title: string;
  alt: string;
  description: string | ReactElement | null;
};

interface ImageGalleryProps extends RouteComponentProps {
  imageList: Image[];
}

function ImageGallery({ imageList }: ImageGalleryProps) {
  const [modal, setModal] = useState<HTMLElement | null>();
  const [galleryIndex, setGalleryIndex] = useState(0);
  const currentVW = useWidthViewPort();

  useEffect(() => {
    setModal(document.getElementById('image-gallery'));
  }, [modal]);

  function handleOpenModal(imagePosition: number) {
    setGalleryIndex(imagePosition);
    modal?.classList.add('open');
  }

  function getImage(imageType: string) {
    switch (imageType) {
      case 'firstGuinness':
        return firstGuinnessPic;
      case 'iceland':
        return icelandPic;
      case 'theWicklowWay':
        return theWicklowWayPic;
      case 'barbara':
        return barbaraPic;
      case 'family':
        return familyPic;
    }
  }

  function handleCloseModal(e: React.MouseEvent<HTMLButtonElement>) {
    e.preventDefault();
    modal?.classList.remove('open');
  }

  function handleNextImageClick(e: React.MouseEvent<HTMLButtonElement>) {
    e.preventDefault();
    console.log('galleryIndex', galleryIndex);
    if (galleryIndex === 4) setGalleryIndex(0);
    else if (galleryIndex < 4) setGalleryIndex(galleryIndex + 1);
  }

  function handlePreviousImageClick(e: React.MouseEvent<HTMLButtonElement>) {
    e.preventDefault();
    if (galleryIndex === 0) setGalleryIndex(4);
    else if (galleryIndex > 0) setGalleryIndex(galleryIndex - 1);
  }

  const buildGalleryThumbnails = () => (
    <ul className='pic-gallery'>
      {imageList.map((image: Image, index: number) => (
        <li className='pic-block'>
          <img
            className='pic-img'
            src={getImage(image.imageType)}
            alt={image.title}
            onClick={() => handleOpenModal(index)}
          />
          <span className='pic-title'>{image.thumbTitle}</span>
        </li>
      ))}
    </ul>
  );

  const buildImageView = () => (
    <div className='modal-content'>
      <button
        className='modal-close'
        onClick={(e: React.MouseEvent<HTMLButtonElement>) =>
          handleCloseModal(e)
        }
      >
        <img src={iconClose} alt='X' className='icon-close' />
      </button>
      <img
        className='opened-img'
        src={getImage(imageList[galleryIndex].imageType)}
        title={imageList[galleryIndex].title}
        alt={imageList[galleryIndex].alt}
      />
      <div className='img-description'>
        <button
          className='img-nav-button'
          onClick={(e: React.MouseEvent<HTMLButtonElement>) =>
            handlePreviousImageClick(e)
          }
        >
          <img src={iconPrevious} alt='<' />
        </button>
        <p className={currentVW <= 420 ? 'small-text' : ''}>
          {imageList[galleryIndex].description}
        </p>
        <button
          className='img-nav-button'
          onClick={(e: React.MouseEvent<HTMLButtonElement>) =>
            handleNextImageClick(e)
          }
        >
          <img src={iconNext} alt='>' />
        </button>
      </div>
    </div>
  );

  return (
    <>
      <div className='modal' id='image-gallery'>
        <div className='modal-bg' style={{ opacity: '70%' }}></div>
        {buildImageView()}
      </div>
      {buildGalleryThumbnails()}
    </>
  );
}

export default ImageGallery;
