// #TODO: Pesquisar sobre retirar o import 'React'.
import React, { useState } from 'react';
import { IntlProvider } from 'react-intl';
import { Router } from '@reach/router';
import { updateWebSiteHeader } from './util';

import Header from './components/Header';
import LangPicker, { Language } from './components/LangPicker';
import Home from './components/Home/';
import About from './components/About/';
import SoftSkills from './components/SoftSkills/';
import Curriculum from './components/Curriculum';
import TechSkills from './components/TechSkills/';
import Footer from './components/Footer';

import './styles.css';
import appTexts from './assets/messages';
import {
  LANG_EN,
  LANG_PT,
  ROUTE_DEFAULT,
  ROUTE_ABOUT,
  ROUTE_SOFT_SKILLS,
  ROUTE_CURRICULUM,
  ROUTE_TECH_SKILLS,
  WEB_SITE_TITLE_ENGLISH,
  WEB_SITE_TITLE_PORTUGUESE,
  WEB_SITE_DESCRIPTION_ENGLISH,
  WEB_SITE_DESCRIPTION_PORTUGUESE,
} from './util/constants';

function App() {
  const [currentLang, setCurrentLang] = useState<Language>(undefined);

  if (!currentLang) {
    try {
      const lang = window.navigator.language;
      if (lang?.indexOf(LANG_EN) > -1) {
        setCurrentLang(LANG_EN);
        updateWebSiteHeader({
          title: WEB_SITE_TITLE_ENGLISH,
          description: WEB_SITE_DESCRIPTION_ENGLISH,
        });
      } else if (lang?.indexOf(LANG_PT) > -1) {
        setCurrentLang(LANG_PT);
        updateWebSiteHeader({
          title: WEB_SITE_TITLE_PORTUGUESE,
          description: WEB_SITE_DESCRIPTION_PORTUGUESE,
        });
      }
    } catch {
      setCurrentLang(LANG_EN);
    }
  }

  return (
    <IntlProvider
      locale={currentLang === LANG_PT ? LANG_PT : LANG_EN}
      messages={currentLang === LANG_PT ? appTexts.pt : appTexts.en}
    >
      <Header
        LangPickerComponent={
          <LangPicker language={currentLang} setCurrentLang={setCurrentLang} />
        }
      />
      {/** #TODO: As rotas fazem com que a página seja recarregada, mas minha ideia
       * é fazer com que o site seja de apenas uma página e o conteúdo transite na
       * horizontal. Pesquisar se é possível utilizar as rotas sem que as páginas
       * sejam recarregadas.
       */}
      <div id='main'>
        <p style={{ textAlign: 'center', fontSize: '40px' }}>
          !!!! TESTING PUBLICATION !!!!
        </p>
        <Router>
          <Home path={ROUTE_DEFAULT} default />
          <About path={ROUTE_ABOUT} />
          <SoftSkills path={ROUTE_SOFT_SKILLS} />
          <Curriculum path={ROUTE_CURRICULUM} />
          <TechSkills path={ROUTE_TECH_SKILLS} />
        </Router>
      </div>
      <Footer />
    </IntlProvider>
  );
}

export default App;
