import React, { useState } from 'react';
import { useIntl } from 'react-intl';
import { RouteComponentProps } from '@reach/router';
import { updateWebSiteHeader } from '../../util';
import useMobileView from '../../hooks/useMobileView';

import brFlagRounded from '../../assets/images/br-flag-rounded.png';
import ukFlagRounded from '../../assets/images/uk-flag-rounded.png';
import arrowDownImg from '../../assets/images/arrow-down.svg';
import closeImg from '../../assets/images/close.svg';
import {
  LANG_EN,
  LANG_PT,
  WEB_SITE_TITLE_ENGLISH,
  WEB_SITE_TITLE_PORTUGUESE,
  WEB_SITE_DESCRIPTION_ENGLISH,
  WEB_SITE_DESCRIPTION_PORTUGUESE,
} from '../../util/constants';
import './styles.css';

export type Language = 'en' | 'pt' | undefined;

interface LangPickerProps extends RouteComponentProps {
  language: Language;
  setCurrentLang: Function;
}

const LangPicker = ({ language, setCurrentLang }: LangPickerProps) => {
  const intl = useIntl();
  const [langOptionsVisible, setLangOptionsVisible] = useState(false);
  const [langOptionsLeftPosition, setlangOptionsLeftPosition] =
    useState<number>();
  const positionFixer = useMobileView() ? 28 : 18;

  function getButtonFlagPosition(el: HTMLImageElement) {
    let x: number = 0;
    while (el && !isNaN(el.offsetLeft) && !isNaN(el.offsetTop)) {
      x += el.offsetLeft - el.scrollLeft;
      el = el.offsetParent as HTMLImageElement;
    }

    return x - positionFixer;
  }

  function updateMenuLangVisibility(): void {
    setlangOptionsLeftPosition(
      getButtonFlagPosition(
        document.getElementById('btn-show-menu-item') as HTMLImageElement
      )
    );
    setLangOptionsVisible(!langOptionsVisible);
  }

  function handleLanguageChange(newLang: string): void {
    setCurrentLang(newLang);
    updateMenuLangVisibility();
    updateWebSiteHeader({
      title:
        newLang === LANG_EN
          ? WEB_SITE_TITLE_ENGLISH
          : WEB_SITE_TITLE_PORTUGUESE,
      description:
        newLang === LANG_EN
          ? WEB_SITE_DESCRIPTION_ENGLISH
          : WEB_SITE_DESCRIPTION_PORTUGUESE,
    });
  }

  const closeMenuItemOptions = (): void => setLangOptionsVisible(false);
  // #TODO: Analisar se não é melhor usar um useEffect para registrar esse evento
  window.addEventListener('resize', closeMenuItemOptions);

  return (
    <section id='lang-menu'>
      <div className='lang-menu-item'>
        <img
          src={language === LANG_PT ? brFlagRounded : ukFlagRounded}
          className='img-flag'
          alt={
            language === LANG_PT
              ? intl.formatMessage({ id: 'portuguese' })
              : intl.formatMessage({ id: 'english' })
          }
          title={
            language === LANG_PT
              ? intl.formatMessage({ id: 'portuguese' })
              : intl.formatMessage({ id: 'english' })
          }
          onClick={updateMenuLangVisibility}
        />
        <img
          id='btn-show-menu-item'
          className='img-options'
          src={langOptionsVisible ? closeImg : arrowDownImg}
          alt={
            langOptionsVisible
              ? intl.formatMessage({ id: 'buttonClose' })
              : intl.formatMessage({ id: 'buttonOpen' })
          }
          title={
            langOptionsVisible
              ? intl.formatMessage({ id: 'buttonClose' })
              : intl.formatMessage({ id: 'buttonOpen' })
          }
          onClick={updateMenuLangVisibility}
        />
      </div>
      <div
        style={{ left: langOptionsLeftPosition }}
        className={
          langOptionsVisible
            ? 'menu-item-options lang-menu-options-visible'
            : 'menu-item-options lang-menu-options-hide'
        }
      >
        {language === LANG_EN ? (
          <img
            src={brFlagRounded}
            className='img-flag'
            alt={intl.formatMessage({ id: 'changeToPortuguese' })}
            title={intl.formatMessage({ id: 'changeToPortuguese' })}
            onClick={() => handleLanguageChange(LANG_PT)}
          />
        ) : (
          <img
            src={ukFlagRounded}
            className='img-flag left-img'
            alt={intl.formatMessage({ id: 'changeToEnglish' })}
            title={intl.formatMessage({ id: 'changeToEnglish' })}
            onClick={() => handleLanguageChange(LANG_EN)}
          />
        )}
      </div>
    </section>
  );
};

export default LangPicker;
